import React from 'react';
import { Route, Routes } from 'react-router-dom';

const Options = React.lazy(() => import('./Options/Options'));
const PullFrom = React.lazy(() => import('./PullFrom/PullFrom'));
const LinkCard = React.lazy(() => import('./LinkCard/LinkCard'));
const PullFundsInfo = React.lazy(() => import('./PullFundsInfo/PullFundsInfo'));
const PullFundsSuccess = React.lazy(() => import('./PullFundsSuccess/PullFundsSuccess'));
const IntrabankForm = React.lazy(() => import('./MoveFundsIntrabank/MoveFundsIntrabank'));
const MoveIntrabankSuccess = React.lazy(() => import('./MoveIntrabankSuccess/MoveIntrabankSuccess'));
const MoveFundsAnotherBank = React.lazy(() => import('./MoveFundsAnotherBank/MoveFundsAnotherBank'));
const MoveFundsAnotherBankInfo = React.lazy(() => import('./MoveFundsAnotherBankInfo/MoveFundsAnotherBankInfo'));
const MoveFundsAnotherBankSuccess = React.lazy(() => import('./MoveFundsAnotherBankSuccess/MoveFundsAnotherBankSuccess'));

function TransactionRouter() {
  return (
    <Routes>
      <Route path="options" element={<Options />} />
      <Route path="from/:option" element={<PullFrom />} />
      <Route path="info/:option/:contactId" element={<PullFundsInfo />} />
      <Route path="success/:option" element={<PullFundsSuccess />} />
      <Route path="linkCard" element={<LinkCard />} />
      <Route path="intrabank/form" element={<IntrabankForm />} />
      <Route path="movefund/success" element={<MoveIntrabankSuccess />} />
      <Route path="otherbank/form" element={<MoveFundsAnotherBank />} />
      <Route path="otherbank/info/:contactId" element={<MoveFundsAnotherBankInfo />} />
      <Route path="otherbank/success" element={<MoveFundsAnotherBankSuccess />} />
    </Routes>
  );
}

export default TransactionRouter;
